import * as C from '../types';
import { get } from '../api';

export const getTeamList = () => async (dispatch, getState, api) => {
	dispatch({
		type: C.FETCH_TEAM_LIST,
	});
	get('/team/getList')
		.then(({ data }) => {
			dispatch({
				type: C.FINISHED_FETCH_TEAM_LIST,
				data,
			});
		})
		.catch((error) => {
			dispatch({
				type: C.ERROR_FETCH_TEAM_LIST,
				error,
			});
		});
};

